<template>
	<view class="user-mobileBind">
		<view class="inpt-wrap row verCenter"><input type="text" value="" placeholder="请输入手机号码" placeholder-style="input-placeholder" v-model="phone" /></view>
		<view class="inpt-wrap row verCenter bothSide">
			<input type="text" value="" placeholder="验证码" placeholder-class="input-placeholder" v-model="smsCode" />
			<button class="code" :value="codetext" @click="getCode()" :class="{ 'code-curr': codeactive }" :disabled="codeactive">{{ codetext }}</button>
		</view>
		<view class="btn" @click="submit">确定</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
export default {
	data() { 
		return {
			phone: '',
			smsCode: '',
			codetext: '获取验证码',
			codeactive: false,
			backpage: '',
			backtype: '',
			params: '',
			url:[]
		};
	},
	onLoad(options) {
		this.backpage = options.backpage;
		this.backtype = options.backtype;
		if (options) {
			
			for (let i in options) {
				if (i !== 'backpage' && i !== 'backtype') {
					this.url.push('&' + i + '=' + options[i]);
				}
			}
		}
	},
	methods: {
		getCode() {
			var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;

			if (!this.phone) {
				uni.showModal({
					title: '提示',
					content: '请输入手机号',
					showCancel: false
				});

				return;
			}

			if (!myreg.test(this.phone)) {
				uni.showModal({
					title: '提示',
					content: '请输入正确手机号',
					showCancel: false
				});

				return;
			}

			this.request(API.sendSms, 'GET', { phone: this.phone, type: 'bind' }, true).then(res => {
				if (res.code === 10000) {
					this.countdDown(); //倒计时开始
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			});
		},
		countdDown() {
			var me = this;
			var wait = 60;
			me.disabled = true;
			me.codeactive = true;
			me.codetext = wait + '秒后获取';
			var clock = setInterval(doLoop, 1000);

			function doLoop() {
				wait--;
				if (wait > 0) {
					me.codetext = wait + '秒后获取';
					me.codeactive = true;
				} else {
					clearInterval(clock);
					me.disabled = false;
					me.codeactive = false;
					me.codetext = '重新获取';
					wait = 60;
				}
			}
		},
		submit() {
			var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;

			if (!this.phone) {
				uni.showModal({
					title: '提示',
					content: '请输入手机号',
					showCancel: false
				});

				return;
			}

			if (!myreg.test(this.phone)) {
				uni.showModal({
					title: '提示',
					content: '请输入正确手机号',
					showCancel: false
				});

				return;
			}

			if (!this.smsCode) {
				uni.showModal({
					title: '提示',
					content: '请输入验证码',
					showCancel: false
				});

				return;
			}

			this.request(API.bindPhone, 'GET', { phone: this.phone, smsCode: this.smsCode }, true).then(res => {
				if (res.code === 10000) {
					if (this.backtype == 1) {
						uni.reLaunch({
							url: this.backpage+'?jump=1' + this.url.join('')
						});
					} else if (this.backtype == 2) {
						uni.switchTab({
							url: this.backpage
						});
					} else {
						uni.switchTab({
							url: '/pages/index/index'
						});
					}
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			});
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/user/mobileBind.scss';
</style>
